import React, { useState, useEffect, Suspense, lazy } from 'react';
import { Droplet, AlertTriangle, Heart, Leaf, Factory, Trash2, Users, ChevronLeft, ChevronRight, Menu, X, ZoomIn, ZoomOut, Maximize2 } from 'lucide-react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
// Firebase imports
import { db, auth } from "./firebase"; // adjust path if needed

// Added auth functions
import { signInWithEmailAndPassword, signOut, onAuthStateChanged } from "firebase/auth";
import NavBar from "./components/NavBar";

// Lazy load pages for better performance
const HomePage = lazy(() => import('./HomePage'));
const CrisisPage = lazy(() => import('./CrisisPage'));
const CausesPage = lazy(() => import('./CausesPage'));
const SolutionsPage = lazy(() => import('./SolutionsPage'));
const MicroplasticsPage = lazy(() => import('./MicroplasticsPage'));
const AboutPage = lazy(() => import('./AboutPage'));
const AdminLogin = lazy(() => import('./AdminLogin'));
const AdminDashboard = lazy(() => import('./AdminDashboard'));

// reCAPTCHA Configuration - Set these only if you want to use reCAPTCHA
// Leave empty strings to disable reCAPTCHA and use direct Firestore increment
const RECAPTCHA_SITE_KEY = "6LdUlwMsAAAAABxfq7hL4xEpg7dV7aZrON9Un38P"; // Set your reCAPTCHA site key here
const RECAPTCHA_FN_URL = "PASTE_YOUR_NEW_FUNCTION_URL_HERE"; // Set your Cloud Function URL here

// Admin configuration - update this with your admin email
const ADMIN_EMAIL = "admin@yourdomain.com"; // Change this to your admin email

const WaterPollutionWebsite = () => {
  const [activeSection, setActiveSection] = useState('home');
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [zoomLevel, setZoomLevel] = useState(1);
  const [isAdmin, setIsAdmin] = useState(false);
  const [showAdminLogin, setShowAdminLogin] = useState(false);
  const [adminCredentials, setAdminCredentials] = useState({
    username: '',
    password: ''
  });
  const [loginError, setLoginError] = useState('');
  const [pageViews, setPageViews] = useState(0);
  const [currentUser, setCurrentUser] = useState(null);

  useEffect(() => {
    // Simulate page view tracking
    const views = parseInt(localStorage.getItem('pageViews') || '0', 10);
    const newViews = views + 1;
    setPageViews(newViews);
    localStorage.setItem('pageViews', newViews.toString());
  }, []);

  const handleAdminLogin = e => {
    e.preventDefault();
    console.log('Login attempt:', adminCredentials); // Debug log

    // Simple admin authentication
    if (adminCredentials.username.trim() === 'admin' && adminCredentials.password.trim() === 'admin123') {
      console.log('Login successful!'); // Debug log
      setIsAdmin(true);
      setShowAdminLogin(false);
      setLoginError('');
      setAdminCredentials({
        username: '',
        password: ''
      });
    } else {
      console.log('Login failed!'); // Debug log
      setLoginError('Invalid credentials. Try username: admin, password: admin123');
    }
  };

  const handleAdminLogout = () => {
    setIsAdmin(false);
    setShowAdminLogin(false);
    setAdminCredentials({
      username: '',
      password: ''
    });
    setLoginError('');
  };

  const floatingAnimation = `
    @keyframes float {
      0%, 100% { transform: translateY(0px) rotate(var(--rotate)); }
      50% { transform: translateY(-20px) rotate(var(--rotate)); }
    }
    .float-animate {
      animation: float 7s ease-in-out infinite;
    }
    @keyframes fade-in {
      from { opacity: 0; transform: translateY(-10px); }
      to { opacity: 1; transform: translateY(0); }
    }
    .animate-fade-in {
      animation: fade-in 0.3s ease-out;
    }
    .transition-smooth {
      transition: all 0.3s ease;
    }
  `;

  const renderSection = () => {
    switch (activeSection) {
      case 'home':
        return <HomePage setActiveSection={setActiveSection} setZoomLevel={setZoomLevel} zoomLevel={zoomLevel} />;
      case 'crisis':
        return <CrisisPage />;
      case 'causes':
        return <CausesPage />;
      case 'solutions':
        return <SolutionsPage />;
      case 'microplastics':
        return <MicroplasticsPage />;
      case 'about':
        return <AboutPage 
          setActiveSection={setActiveSection} 
          isAdmin={isAdmin}
          showAdminLogin={showAdminLogin}
          setShowAdminLogin={setShowAdminLogin}
          handleAdminLogin={handleAdminLogin}
          adminCredentials={adminCredentials}
          setAdminCredentials={setAdminCredentials}
          loginError={loginError}
          handleAdminLogout={handleAdminLogout}
          pageViews={pageViews}
          activeSection={activeSection}
          zoomLevel={zoomLevel}
        />;
      case 'admin-login':
        return <AdminLogin onLogin={handleAdminLogin} />;
      case 'admin':
        return isAdmin ? (
          <AdminDashboard pageViews={pageViews} activeSection={activeSection} zoomLevel={zoomLevel} onLogout={handleAdminLogout} currentUser={currentUser} />
        ) : (
          <AdminLogin onLogin={handleAdminLogin} />
        );
      default:
        return <HomePage setActiveSection={setActiveSection} setZoomLevel={setZoomLevel} zoomLevel={zoomLevel} />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <style>{floatingAnimation}</style>
      <NavBar 
        activeSection={activeSection} 
        setActiveSection={setActiveSection} 
        isAdmin={isAdmin}
        mobileMenuOpen={mobileMenuOpen}
        setMobileMenuOpen={setMobileMenuOpen}
      />
      <main>
        <Suspense fallback={<div className="flex justify-center items-center min-h-screen">Loading...</div>}>
          {renderSection()}
        </Suspense>
      </main>
      <footer className="bg-gradient-to-r from-blue-900 to-blue-700 text-white py-6 sm:py-8 mt-12 sm:mt-16">
        <div className="container mx-auto px-4 text-center">
          <p className="text-base sm:text-lg mb-2">Quirk Lab - Water Pollution Awareness Project</p>
          <p className="text-sm sm:text-base">Dhole Patil College of Engineering | Department of Information Technology</p>
          <p className="mt-3 sm:mt-4 text-xs sm:text-sm">Academic Year 2025-26 | Project Review-I</p>
          <p className="mt-3 sm:mt-4 font-bold text-sm sm:text-base">Let's Protect Our Rivers Together!</p>
        </div>
      </footer>
    </div>
  )
};

export default WaterPollutionWebsite;